import React, { Component } from 'react';

import Home from './Home';
import AboutMe from './AboutMe';
import Projects from './Projects';
import Contact from './Contact';

class Router extends Component {
    render() {
        return (
            <React.Fragment>
<Home />
         <AboutMe />
         <Projects />
         <Contact />
            </React.Fragment>
         
        )
    }
}

export default Router;